@import url(https://fonts.googleapis.com/css2?family=Sigmar+One&display=swap);
.App {
  font-family: sans-serif;
  text-align: center;
}
.head {
  width: 500px;
  height: 150px;
}
header {
  background-color: #fbc531;
}
.btn {
  background-color: #2d3436;
  color: aliceblue;
  height: 35px;
  width: 100px;
  border-radius: 20px;
  font-size: 15px;
}
input {
  height: 50px;
  width: 220px;
  font-size: 20px;
  margin-left: 30px;
}
.toptxt {
  position: relative;
  top: 6px;
  background-color: white;
  color: #2d3436;
  font-family: "Sigmar One", cursive;
}
.bottomtxt {
  position: relative;
  bottom: 80px;
  background-color: white;
  color: #2d3436;
  font-family: "Sigmar One", cursive;
}

